.main {
  position: relative;
  z-index: 2;
  padding-top: 64px;
  // margin-top: -64px;

  a {
    color: #fff;
  }

  &-images {
    line-height: 0;

  }

  &-bg {
    position: relative;
    z-index: 1;
    width: 100%;

    img {
      max-height: 100vh;
      width: 100%;
      object-fit: cover;
    }

    &-up {
      line-height: 0;
      position: absolute;
      bottom: 0px;
      z-index: 3;
      width: 100%;

      img {
        max-height: 100vh;
        width: 100%;
        object-fit: cover;
      }
    }

    img {
      width: 100%;
      height: 100%;
    }
  }

  &-text {
    position: fixed;
    z-index: 2;
    top: 106px;
    text-align: center;
    width: 100%;
    line-height: 0;
    padding: 0 40px;
  }

  &-info {
    position: absolute;
    bottom: 150px;
    z-index: 4;
    width: 100%;
    display: flex;
    align-items: flex-start;
    padding-left: 40px;
    justify-content: space-between;
    padding-right: 10px;
    max-width: 1600px;
    left: 50%;
    transform: translate(-50%, 0);

    .ui-title {
      color: #fff;
      max-width: 890px;
      padding-right: 15px;
    }

    .ui-btn {
      display: none;
    }
  }

  &-block {
    background: #910828;
    width: 100%;
    padding: 30px;
    border-radius: 8px;
    max-width: 530px;
    margin-bottom: 2px;

    &-logo {
      line-height: 0;
      margin-bottom: 33px;
      display: inline-block;
      width: 135px;
      height: 135px;
      border-radius: 50%;
      border: 1px solid rgba(255, 255, 255, 0.2);
      position: relative;

      img {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
      }
    }

    &-desc {
      font-weight: 500;
      font-size: 18px;
      line-height: 1.3;
      color: #fff;
      max-width: 370px;
      margin-bottom: 20px;
    }

    &-link {
      font-weight: 500;
      font-size: 60px;
      line-height: 1.1;
      letter-spacing: -0.02em;
      text-transform: uppercase;
      font-family: 'Oswald';
      position: relative;

      &::after {
        content: "";
        position: absolute;
        left: 0;
        bottom: -4px;
        width: 0;
        height: 3px;
        background-color: #fff;
        transition: width .4s ease;
      }

      &:hover::after {
        width: 100%;
      }
    }
  }
}


.category {
  // background-color: #000;
  // padding-bottom: 2px;
  position: relative;
  z-index: 4;
  box-shadow: 0 20px 40px 0 rgba(0, 0, 0, 0.35);
  top: -150px;
  margin-bottom: -150px;

  .ui-title {
    padding-right: 15px;
  }

  &::after {
    content: "";
    width: 100%;
    height: calc(100% - 150px);
    background-color: #000;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: -1;
  }

  .splide__arrows.splide__arrows--ltr {
    line-height: 0;
    position: relative;
    bottom: -9px;
    font-size: 0;
  }

  &-content {
    // padding: 110px 30px;
    // margin: 0 10px;
    background-color: #fff;
    border-radius: 8px;
  }

  &-head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 50px;
  }

  &-card {
    max-width: 500px;
    position: relative;

    .ui-btn {
      position: absolute;
      bottom: 30px;
      width: calc(100% - 60px);
      left: 30px;
      opacity: 0;
      transform: translateY(20px);
      transition: opacity .4s ease, transform .4s ease, box-shadow ease .4s, color ease .4s;
    }

    &:hover {
      .ui-btn {
        opacity: 1;
        transform: translateY(0);
      }
    }

    &-head {
      position: relative;
      z-index: 1;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }

    &-count {
      font-weight: 400;
      font-size: 14px;
      line-height: 1.2;
      letter-spacing: 0.01em;
      color: #878B95;
      background-color: #E3E6E8;
      height: 36px;
      line-height: 36px;
      border-radius: 24px;
      padding: 0 16px;
      text-align: center;
      font-family: "Oswald";
      white-space: nowrap;
    }

    &-content {
      max-width: 500px;
      min-height: 430px;
      border-radius: 8px;
      border: 1px solid #CCD2D6;
      position: relative;
      padding: 30px;
      overflow: hidden;
      background-color: #fff;
    }

    &-title {
      font-weight: 500;
      font-size: 32px;
      line-height: 1.1;
      letter-spacing: -0.01em;
      text-transform: uppercase;
      font-family: "Oswald";
      margin-bottom: 30px;
      color: #121317;
    }

    &-subtitle {
      font-weight: 500;
      font-size: 18px;
      line-height: 1.3;
      font-family: "Manrope";
    }

    &-img {
      line-height: 0;
      position: absolute;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 100%;

      img {
        object-position: right bottom;
        width: 100%;
        height: 100%;
        position: relative;
        z-index: 0;
        object-fit: contain;
      }
    }

    &-btns {
      position: absolute;
      bottom: 30px;
      width: 100%;
      padding: 0 30px;
      display: flex;

      .ui-btn {
        width: 50%;

        &:first-child {
          margin-right: 16px;
        }
      }
    }
  }
}

.advantages {
