.cart {
  color: #121317;

  a {
    color: #121317;
  }

  &-wrapper {
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    z-index: 50;
    bottom: 0;
    display: none;

    &.active {
      display: block;
    }
  }

  &-block {
    max-width: 530px;
    position: absolute;
    right: 10px;
    bottom: 10px;
    /* bottom: 10px; */
    // height: calc(100% - 20px);
    height: calc(100% - 86px);
    background: #F3F4F6;
    border-radius: 8px;

    &-head {
      padding: 30px;
      font-weight: 700;
      font-size: 64px;
      line-height: 1.2;
      letter-spacing: -0.02em;
      text-transform: uppercase;
      font-family: "Oswald";
      border-bottom: 1px solid #CCD2D6;
      margin-bottom: 30px;
    }

    &-body {
      padding: 0 30px;
      margin-bottom: 30px;
      height: calc(100% - 294px);
      margin-bottom: 30px;
      overflow-x: hidden;
      overflow-y: auto;
    }

    &-link {
      // position: absolute;
      // bottom: 30px;
      padding: 0 30px;
      width: 100%;

      a {
        width: 100%;
      }
    }
  }

  &-item {
    display: flex;
    position: relative;
    background: #fff;
    padding: 5px;
    border-radius: 8px;
    margin-bottom: 5px;
    padding-right: 42px;

    &:last-child {
      margin-bottom: 0;
    }

    &-img {
      display: block;
      flex: 0 0 120px;
      max-width: 120px;
      height: 120px;
      line-height: 0;
      border: 1px solid #F3F4F6;
      border-radius: 4px;
      margin-right: 10px;

      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }

    &-title {
      font-weight: 500;
      font-size: 24px;
      line-height: 1.3;
      text-transform: uppercase;
      max-width: calc(100% - 130px);
      flex: 0 0 calc(100% - 130px);
      text-wrap: balance;

      a {
        font-family: "Oswald";
        color: #121317;
      }
    }

    &-remove {
      position: absolute;
      right: 0;
      top: 0;
      border: none;
      background-color: transparent;
      width: 32px;
      height: 32px;
      top: 5px;
      right: 10px;
      padding: 0;
      cursor: pointer;
    }
  }

  &-close {
    position: absolute;
    top: 46px;
    left: -66px;
    background: #F3F4F6;
    border: none;
    width: 46px;
    height: 46px;
    border-radius: 50%;
    padding: 0;
    line-height: 0;
    cursor: pointer;

    img {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }
}

