        font-size: 12px;
      }

      &-content {
        min-height: 270px;
        padding: 20px;
      }

      &-btns {
        flex-direction: column;

        .ui-btn {
          width: 100%;
          margin-bottom: 5px;

          &:first-child {
            margin-right: 0;
            margin-bottom: 5px;
          }
        }
      }
    }

    &-content {
      overflow: hidden;
    }
  }

  .trust {
    &-content {
      // padding: 50px 10px;
      overflow: hidden;
    }


    &-list {
      margin: 0 -30px;
    }

    &-content {
      .splide__slide:nth-child(n+1) .trust-item:last-child  {
        border-bottom: none;
      }
    }

    &-item {
      width: 100%;
      padding: 30px;
      border-bottom: 1px solid #CCD2D6;
      height: 110px;

      img {
        position: relative;
        width: 80%;
        height: 80%;
      }

      &-block {
        display: block;
        max-width: none;
      }
    }
  }

  .advantages {
    &-content {
      // margin-left: 0;
      // margin-right: 0;
      // padding: 50px 10px;
      margin-bottom: -50px;
      top: -50px;
    }

    &-body {
      &-list {
        flex-wrap: wrap;
        flex-direction: column;
      }

      &-img {
        padding-top: 29px;
      }

      &-item {
        max-width: 100%;
        border-bottom: 1px solid #4c4c4c;
        border-right: none;
        height: 210px;

        &:last-child {
          border-bottom: none;
        }
      }

      &-cards {
        flex-direction: column;
      }
    }

    &-card {
      padding-top: 50px;
      padding-bottom: 20px;
      max-width: 100%;
      border-right: none;
      border-bottom: 1px solid #4C4C4C;
      min-height: 210px;

      &:last-child {
        border-bottom: none;
      }

      &-img {
        max-width: 64px;
        margin: 0 auto;
        line-height: 0;
      }

      &-title {
        max-width: none;
      }
    }
  }

  .main {
    padding-top: 74px;
    margin-top: -74px;

    &-info {
      padding: 0 10px;
      bottom: 38px;
    }

    &-text {
      padding: 0 10px;
      // top: 152px;
      top: calc(25% - 74px);
    }

    &-block {
      display: none;
    }

    &-info {
      margin-bottom: 50px;

      .ui-btn {
        display: block;
        width: 100%;
        height: 58px;
        line-height: 58px;
        font-size: 14px;
      }

      .ui-title {
        margin-bottom: 20px;
      }
    }
  }

  .cart {
    // &-wrapper {
    //   height: calc(100% - 74px);
    // }

    &-block {
      max-width: none;
      top: auto;
      height: 100%;
      right: auto;
      bottom: auto;
      width: 100%;
      border-radius: 0;
      height: calc(100% - 74px);
      bottom: 0;

      &-head {
        font-size: 30px;
        padding: 10px;
        padding-bottom: 20px;
        margin-bottom: 20px;
      }

      &-body {
        padding: 0 10px;
        height: calc(100% - 185px);
      }

      &-link {
        padding: 0 10px;

        .ui-btn {
          height: 58px;
          line-height: 58px;
        }
      }
    }

    &-close {
      left: auto;
      right: 10px;
      background: #121317;
      top: 10px;

      img {
        filter: brightness(0) invert(1);
      }
    }

    &-item {
      &-title {
        font-size: 18px;
        max-width: calc(100% - 88px);
        flex: 0 0 calc(100% - 88px);
      }

      &-img {
        flex: 0 0 78px;
        max-width: 78px;
        height: 78px;

      }
    }
  }
}