p {
  margin-top: 0;
  margin-bottom: 20px;
}

ul,
ol,
li {
  padding: 0px;
  margin: 0px;
  list-style: none;
}

ul, ol {
  margin-bottom: 20px;
}

h1,
h2,
h3,
h4,
h5,
.h1,
.h2,
.h3,
.h4,
.h5 {
  font-family: $baseFont;
  color: #000;
  text-transform: none;
  line-height: 1.42;
  font-weight: 700;
  margin-bottom: 20px;
}

h1,
.h1 {
  font-size: 2.5rem;
}

h2,
.h2 {
  margin-top: 30px;
  font-size: 2rem;
}

h3,
.h3 {
  margin-top: 30px;
  font-size: 1.375rem;
}

h4,
.h4 {
  margin-top: 30px;
  font-size: 1.125rem;
}

h5,
.h5 {
  margin-top: 30px;
  font-size: 1rem;
}

a {
  color: $linkColor;
  text-decoration: none;
  outline: none;
  position: relative;

